.AppHeader {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Form {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 1rem;
}

.H1 {
    color: black;
    text-align: center;
}

.url input {
    font: inherit;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 20rem;
    max-width: 100%;
}

.url button {
    font: inherit;
    color: white;
    background-color: #8b2c06;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    margin-left: 0.5rem;
}

.url button:hover,
.url button:active {
    background-color: #5a1a01;
}

.custom {
    color: black;
    font-size: 1rem;
    margin-top: 1rem;
}

.custom input {
    border-radius: 5px;
    border: 1px solid #ccc;
    font: inherit;
}

.post {
    color: black;
    display: flex;
    font-size: 1.2rem;
    border: 1px dashed red;
    display: flex;
}